e PCB with the microchip mounted on, the stepper motor itself, and the driver board for the motor. ")]),_c('div',{staticClass:"prototypeDesign third"},[_vm._v(" The motor is tightly secured by two opposing walls, as well as a notched wall on the top, hindering it in moving vertical. The main gear is mounted on the stepper motors axle, vertically, and held securely by a wheel baring on the opposing side, enabling steady rotation of the gear. Parallel to the main gear, there is a second gear mounted to two wheel barrings, one below and one above the gear. The two gears have a similar additional outer gear on their top side, which interconnects, making the second gears rotation fully dependent on the main gears rotation. ")]),_c('div',{staticClass:"prototypeDesign fourth"},[_vm._v(" Between the two gears there is a 2mm gap, where the belt itself is feed through. The gap is tighter than the belt itself, and therefore grabs the belt as it is feed through the opening and spools is further in. This leads to the two cutouts for the belt to go through, on the left and right side of the belt buckle. These cutouts are aligned, so the belt is spooled through on a parallel path as where the two gears are mounted on the inside of the box. ")]),_c('div',{staticClass:"prototypeDesign fifth"},[_vm._v(" On the inside of the box, there is a second compartment, located in between the outer wall and a partition wall north of the main gear. This compartment is reserved for housing the PCB board and the driver board. At last, there is a cutout on the left upper corner of the wall, where the start tip of the belt is mounted on. ")]),_vm._m(2),_vm._m(3),_vm._m(4)])])}
