ke as he is the only one also located in Aarhus. The second frame [Frame 2], show a small profile section in the top covering general info about mike. Below that we can see the recent messages they have exchanged. Furthermore, it depicts Anna having sent a message asking Mike out for coffee the next day, and Mike have just become active, currently typing a response to Anna. "),_c('br'),_vm._v(" "),_c('br'),_vm._v(" In our second mock-up [Frame 3], a man named Chris have just moved to Copenhagen from Stockholm, and have just set his profile up with the app. Being new in town he want to socialize and invite some people out on dinner. Hence, he creates an event that will be displayed on the map in the Happenings section of the app, that people in the nearby location can see and join. He has some options to specify the event, What is the event, For whom, At what time and the following location of the event. Completing the post he press the \"Post\" button. Following that action, the app gives him an alert dialogue, informing him that his post was successfully uploaded (This Frame is not included). He presses the \"Ok\" button on the alert dialogue and is the redirected to \"Happenings\" section of our app [Frame 4], where the map shows him that location of the event as well as a description of the event. ")])]),_c('div',{staticClass:"mediaBox",attrs:{"lang":"img"}},[_c('h5',[_vm._v("Personal profile page")]),_c('img',{attrs:{"src":"https://i.imgur.com/f3FD26J.png?4"}})]),_c('div',{staticClass:"mediaBox",attrs:{"lang":"img"}},[_c('h5',[_vm._v("Chat window")]),_c('img',{attrs:{"src":"https://i.imgur.com/OkzAhvy.png?2"}})]),_c('div',{staticClass:"mediaBox",attrs:{"lang":"img"}},[_c('h5',[_vm._v("Creating event")]),_c('img',{attrs:{"src":"https://i.imgur.com/DqtWg5y.png"}})]),_c('div',{staticClass:"mediaBox",attrs:{"lang":"img"}},[_c('h5',[_vm._v("Event appears on map")]),_c('img',{attrs:{"src":"https://i.imgur.com/bixZdgt.png"}})]),_c('div',{staticClass:"mediaBox"},[_c('h5