var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"illustration"},[_c('img',{attrs:{"src":"https://i.imgur.com/5TTsONy.png","title":"source: imgur.com"}}),_c('p',[_vm._v("Scenario that illustrates the protptype")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"elaboration"},[_c('h4',[_vm._v("How it works")]),_vm._v(" The game works as following. Four pupils sits around the device. Then they push the main button on the top of the device, which makes four different words appear, where they only can see one word each. These are the main words. They all have three corresponding help words, which is related to the meaning of the main word. Then they push the button again, and the device will choose one of those four words. The pupil that the word is displayed for, will be instructed to explain that. When they press the button again, there will appear a help word, connected to the chosen word, on the three other screens. Help words will appear one at a time, to secure that they can discuss and connect it to the chosen word. After this is done, they can press the button again, to record the explanation of the word. When this is done, the explanation is stored on the website, where they can find it connected to the chosen word. From the teachers perspective, we learned that she has had some quite struggling experiences with new technology, so we have made our website it in a way, where she only redefines the main words and help words. "),_c('br'),_c('br'),_c('h4',[_vm._v("Empirical data")]),_vm._v(" From the observations we made visiting an Elementary school, sixth grade english class, we got different views on the pupils and their work methods. We wrote notes about how they acted and used the English language compared to the Danish. We also saw, how they cooperated with each other and how they helped each other. Furthermore, we saw how the the pupil’s relation with the teacher was like. Information was gained with the methods from Ethnographic field methods, Guiding Principles of Ethnography(NaturalSettings). We also had small conversations and semi-structured interviews with the teacher and pupils which gave an insight in their daily work and activities. "),_c('br'),_c('br'),_c('h4',[_vm._v("Concept")]),_vm._v(" It is the pupils themselves, who records these audio files. The game is suppose to encourage the pupils, to collaborate with their classmates, to make the most substantial description of every word, that they receive. The goal for this game, is not to make a flawless sentence, without any grammatical mistakes. It is suppose to make you practice your verbal skills, to gain experience in explaining specific things. It is equal to what is expected of you in the demands from the ministry in Denmark. From our collection of empirical data, we have learned that they use their native language more frequently, when discussing the meaning of words. It is hard to control, whether the pupils speak Danish or English, when the teacher is not nearby. It gave them an advantage of a common understanding, when they discuss the words. ")])}]

